/*
**  define styling for element component with specified with classe  main header.
*/
.header {
	border-bottom: map-get($space, s8) solid map-get($color-grey, l4);
	@include boxShadow(main);
	position: absolute;
	top: 0;
	top: 0;
	left: 0;
	right: 0;
	z-index: map-get($z-index, xxl1);
	background-color: $color-white;

	&.sticky {
		position: fixed;
		background-color: rgba($color-white, 0.85);
	}

	&__content {
		padding: map-get($space, s2) map-get($space, l2);
		display: flex;
		justify-content: space-between;
	}

	&__logo {
		font-size: map-get($font-size, l5);
		font-weight: map-get($font-weight, main);
		text-transform: uppercase;
		letter-spacing: map-get($space, s7);
		color: map-get($color-grey, d1);

		&__link {
			display: flex;
			align-items: center;
			gap: map-get($space, s3);
			color: inherit;

			&__image {
				width: map-get($space, l3);
			}
		}
	}

	&__nav {
		flex-grow: 1;

		@include rwd-df(md) {
			transform: translateX(100%) scale(0);
			transition: all map-get($time, l3) * 2;
			opacity: 0;
			transform-origin: center right;

			&.open {
				transition: all map-get($time, l3) * 2;
				transform: translate(0) scale(1);
				opacity: 1;
			}
		}

		font-size: map-get($font-size, l2);
		font-weight: map-get($font-weight, l2);

		@include rwd-df(md) {
			font-size: map-get($font-size, l5);
			position: fixed;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			z-index: map-get($z-index, xxl1);

			display: flex;
			align-items: center;
			justify-content: center;
			background-color: rgba(map-get($color-grey, l6), 0.98);
		}

		&__content {
			display: flex;
			justify-content: space-between;
			align-items: center;
			height: 100%;
			@include rwd-df(md) {
				flex-direction: column;
				gap: map-get($space, l4);
				height: auto;
			}
		}

		&__links-list {
			margin: 0 auto;
			display: flex;
			align-items: center;
			gap: map-get($space, l1);

			@include rwd-df(md) {
				flex-direction: column;
				gap: map-get($space, l3);
			}

			&__item {
				&__link {
					display: block;
					&:link,
					&:visited {
						color: map-get($color-grey, main);
						text-transform: uppercase;
						transition: map-get($time, main);
					}
					&:hover,
					&:active,
					&.active {
						color: map-get($color-primary, main);
					}
				}
			}
		}

		&__icons-list {
			display: flex;
			align-items: center;
			gap: map-get($space, main);
			@include rwd-df(md) {
				gap: map-get($space, l4);
			}

			&__item {
				&__link {
					display: flex;
					align-items: center;
					justify-content: center;

					&:link,
					&:visited {
						color: map-get($color-grey, main);
						text-transform: uppercase;
						transition: map-get($time, main);
					}
					&:hover,
					&:active,
					&.active {
						color: map-get($color-primary, main);
					}

					&__icon {
						width: map-get($space, l3);
						color: inherit;
					}
				}
			}
		}
	}
}

