.scroll-to-top-btn {
	position: fixed;
	bottom: 5rem;
	#{$end}: 3rem;
	padding: map-get($space, s5);
	background-color: map-get($color-primary, d1);
	color: map-get($color-primary, l5);
	@include squareSize(calc(map-get($space, l5) / 2));
	font-size: map-get($font-size, main);
	border-radius: map-get($space, s3);
	font-weight: map-get($font-weight, l3);
	line-height: 0;
	display: flex;
	justify-content: center;
	align-items: center;

	display: block;
	visibility: hidden;
	opacity: 0;
	pointer-events: none;
	transform: scale(0);
	transition: all map-get($time, l3);
	border: 0.2rem solid transparent;

	&.show {
		visibility: visible;
		opacity: 1;
		transform: scale(1);
		pointer-events: auto;

		&:hover {
			background-color: map-get($color-primary, l5);
			color: map-get($color-primary, d1);
			border-color: map-get($color-primary, d1);
			border-radius: 0;
		}
	}
}
