.slider {
	background-color: #add4ff;
	position: relative;
	height: 100%;
	overflow-x: hidden;
	&__slides {
		position: relative;
		height: 100%;
	}

	&__slide {
		display: none;
		justify-content: center;
		align-items: center;
		font-size: map-get($font-size, l6);
		font-weight: map-get($font-weight, l3);
		padding-top: 5%;
		padding-bottom: 5%;

		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		transition: transform map-get($time, s1);
		@include backgroundImage;

		&--prev,
		&--curr,
		&--next {
			display: flex;
		}

		&--prev {
			transform: translateX(-100%);
		}

		&--curr {
			transform: translateX(0%);
		}

		&--next {
			transform: translateX(100%);
		}

		&__fig {
			position: relative;
			height: 100%;
			width: 100%;
			display: flex;
			justify-content: center;
			align-items: center;

			&__image {
				height: 60%;
				width: auto;

				@include rwd-df(md) {
					height: 50%;
				}

				@include rwd-df(sm) {
					height: auto;
					width: 80%;
				}

				// width: 50%;
				// height: auto;

				// @include rwd-df(md) {
				// 	width: 60%;
				// }

				// @include rwd-df(md) {
				// 	width: 80%;
				// }
			}

			&__info {
				position: absolute;
				bottom: 0;
				#{$start}: 0;

				@include rwd-df(sm) {
					margin: map-get($space, l3);
				}

				@extend .u-heading-font;
				text-transform: capitalize;
				text-align: center;

				padding: map-get($space, main);
				color: map-get($color-grey, main);
				background-color: rgba($color-white, 0.5);
				border-radius: map-get($space, main);

				&__des {
					font-size: map-get($font-size, l1);
					font-weight: map-get($font-weight, main);
				}
				&__name {
					color: map-get($color-grey, d2);
					font-size: map-get($font-size, l7);
					font-weight: map-get($font-weight, l1);
					margin: map-get($space, s5);
				}
				&__price {
					display: block;
					font-size: map-get($font-size, l1);
				}
				&__btn {
					margin-top: map-get($space, l1);
					font-size: map-get($font-size, l1);
					color: map-get($color-grey, d2);
					text-transform: uppercase;
					padding: 0.7rem 6rem;
					opacity: 0.75;
					border-radius: 96px;
					border: none;
					box-shadow: 0 2px 8px 0 rgb(0 0 0 / 8%);
					background-color: $color-white;
					transition: map-get($time, main);

					&:hover {
						color: map-get($color-primary, main);
						background-color: map-get($color-grey, d2);
					}
				}
			}
		}
	}

	&__navigator {
		position: absolute;
		@include rwd-df(sm) {
			// position: static;
			flex-direction: column;
		}
		bottom: 10%;
		#{$end}: 5%;
		display: flex;
		align-items: center;
		gap: map-get($space, l1);

		&__arrow {
			width: map-get($space, l3);
			height: map-get($space, l3);
			font-size: map-get($font-size, l4);
			font-weight: map-get($font-weight, l2);
			border-radius: 50%;
			background-color: #fff;
			color: map-get($color-grey, l1);
			border: none;
			transition: all map-get($time, main);
			box-shadow: 0 2px 8px 0 rgb(0 0 0 / 1%);

			display: flex;
			align-items: center;
			justify-content: center;

			@include rwd-df(sm) {
				border-radius: map-get($space, s3);
			}

			// @include rwd-df(sm) {
			// 	border-radius: map-get($space, s3);
			// 	position: absolute;
			// 	top: 50%;
			// 	transform: translateY(-50%);

			// 	&--prev {
			// 		#{$start}: map-get($space, l1);
			// 	}
			// 	&--next {
			// 	}
			// 	#{$end}: map-get($space, l1);
			// }

			&:hover {
				box-shadow: 0 2px 8px 0 rgb(0 0 0 / 20%);
				border-color: $color-white;
				background-color: $color-white;
				color: map-get($color-grey, d1);
			}
		}

		&__indicators-list {
			display: flex;
			gap: map-get($space, main);

			@include rwd-df(sm) {
				display: none;
			}

			&__item {
				width: map-get($space, l1);
				height: map-get($space, s6);
				border-radius: map-get($space, main);
				background-color: map-get($color-grey, l7);
				transition: all map-get($time, main);
				cursor: pointer;
				// box-shadow: 0 2px 8px 0 rgb(0 0 0 / 1%);
				box-shadow: 0 2px 8px 0 rgb(0 0 0 / 20%);

				&:hover,
				&.active {
					background-color: map-get($color-grey, d1);
					box-shadow: 0 2px 8px 0 rgb(0 0 0 / 20%);
				}
			}
		}
	}
}
