.products-sec {
	&__title {
		text-align: center;
		font-size: map-get($font-size, l6);
		color: map-get($color-grey, l3);
		@extend .u-text-font;
		font-style: italic;
		font-weight: map-get($font-weight, main);
		margin-bottom: map-get($space, l5);

		span {
			color: map-get($color-grey, d2);
		}
	}

	&__content {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-auto-flow: row dense;
		gap: map-get($space, main);

		@include rwd-df(md) {
			grid-template-columns: repeat(2, 1fr);
		}

		@include rwd-df(xs) {
			grid-template-columns: repeat(1, 1fr);
		}

		.product-card.two {
			grid-column: span 2;
			@include rwd-df(xs) {
				grid-column: span 1;
			}
		}
	}
}
