/*
**  define utility classes to set headeing font
*/
.u-heading-font {
	font-family: map-get($font-family, primary), sans-serif;
}

/*
**  define utility classes to set text font
*/
.u-text-font {
	font-family: map-get($font-family, secondary), sans-serif;
}

/*
**  define utility classes to make clearfix for specified floated elements
*/
.u-clearfix::after {
	content: '';
	display: block;
	clear: both !important ;
}

/*
  **  define utility classes to hidden specified elements
  */
.u-hidden {
	display: none !important;
}

/*
**  define utility class for center inline & inline-block specified elements
*/
.u-center-text {
	text-align: center !important ;
}

/*
**  define utility class for center block element specified elements
*/
.u-absCenter {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

/*
  **  define utility class for add overlayer specified elements
  */
.u-overlayer::after {
	content: '';
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba($color-black, 0.7);
	z-index: 2;
}

/*
**  define utility class for background colors
*/
.u-bg-purple {
	background-color: #e6e2f0 !important;
}

.u-bg-grey {
	background-color: #e5e7eb !important;
	// background-color: #f5f5f5 !important;
}

.u-bg-blue {
	background-color: #add4ff !important;
}

.u-bg-pink {
	background-color: #ebd9d9 !important;
}
