.social-list {
	display: flex;
	gap: map-get($space, main);
	margin-top: map-get($space, s2);
	&__item {
		&__link {
			.icon {
				background-color: $color-white;
				width: map-get($space, l2);
				height: map-get($space, l2);
				padding: map-get($space, s5);
				border-radius: map-get($space, s5);
				transition: map-get($time, main);
				opacity: 0.75;

				&:hover {
					opacity: 1;
				}

				&.icon-facebook {
					color: #3b5998;
				}

				&.icon-twitter {
					color: #1da1f2;
				}

				&.icon-instagram {
					color: #405de6;
				}

				&.icon-youtube-play {
					color: #ff0000;
				}
			}
		}
	}
}
