/*
**  define styling for element components with specified with classe main footer.
*/

.footer {
	background-color: map-get($color-grey, d2);
	&-main {
		padding: map-get($space, l3) 0;

		&__content {
			display: grid;
			grid-template-columns: repeat(6, 1fr);
			gap: map-get($space, l2);

			@include rwd-df(md) {
				grid-template-columns: repeat(3, 1fr);
				gap: map-get($space, l3);
			}

			@include rwd-df(xs) {
				gap: map-get($space, l4);
				grid-template-columns: repeat(2, 1fr);
			}
		}

		hr {
			height: 0.5px;
			border: none;
			background-color: map-get($color-grey, main);
			grid-column: 1 / -1;
			@include rwd-df(xs) {
				// display: none;
			}
		}

		.footer-col {
			&__title {
				color: #fff;
				text-transform: uppercase;
				font-size: map-get($font-size, l1);
				font-weight: map-get($font-weight, l2);
				margin-bottom: map-get($space, l1);
			}

			&--contact-info {
				grid-column: span 2;

				@include rwd-df(md) {
					grid-area: 2 / 2 / 3 / 4;
				}

				@include rwd-df(xs) {
					grid-area: 3 / 1 / 4 / 3;
				}

				&__list {
					display: flex;
					flex-direction: column;
					gap: map-get($space, main);
					&__item {
						font-size: map-get($font-size, main);
						color: map-get($color-grey, l5);
						display: block;
						font-weight: map-get($font-weight, l1);

						&__link {
							&:link,
							&:visited {
								font-size: map-get($font-size, s1);
								color: map-get($color-grey, l4);
								margin-#{$start}: map-get($space, s3);
								transition: all map-get($time, main);
								text-decoration: underline;
							}

							&:hover,
							&:active {
								color: map-get($color-primary, main);
							}
						}
					}
				}
			}

			&--newsletters {
				grid-column: span 2;

				@include rwd-df(md) {
					grid-area: 1 / 2 / 2 / 4;
				}

				@include rwd-df(xs) {
					grid-area: 2 / 1 / 3 / 3;
				}

				&__text {
					color: map-get($color-grey, l5);
					margin-bottom: map-get($space, main);
					font-size: map-get($font-size, main);
				}

				&__newsletters-form {
					input[type='email'] {
						display: block;
						width: 100%;
						border-radius: 0;
						border: map-get($space, s8) solid map-get($color-grey, l5);
						font-size: map-get($font-size, l1);
						padding: map-get($space, s5) map-get($space, s1);
						background-color: map-get($color-grey, l6);
						margin-bottom: map-get($space, main);
						@include rwd-df(md) {
							width: 75%;
						}
						@include rwd-df(xs) {
							width: 85%;
						}
					}

					button[type='submit'] {
						font-size: map-get($font-size, l1);
						border-radius: 0;
						background-color: map-get($color-primary, main);
						color: map-get($color-grey, l7);
						padding: map-get($space, s3) map-get($space, l2);
						text-transform: capitalize;
						transition: all map-get($time, main);
					}
				}
			}

			&--images {
				grid-column: span 3;
				@include rwd-df(xs) {
					grid-column: span 2;
				}
				.footer__images-list {
					display: flex;
					flex-wrap: wrap;

					gap: map-get($space, main);
					&__item {
						&__image {
							height: map-get($space, l3);
						}
					}
				}
			}
		}

		.footer {
			&__links-list {
				display: flex;
				flex-direction: column;
				gap: map-get($space, main);
				&__item {
					&__link {
						&:link,
						&:visited {
							font-size: map-get($font-size, main);
							color: map-get($color-grey, l5);
							text-transform: capitalize;
							transition: all map-get($time, main);
						}
						&:hover,
						&:active {
							color: map-get($color-primary, main);
							text-decoration: underline;
						}
					}
				}
			}
		}
	}

	&-sub {
		font-size: map-get($font-size, main);
		font-weight: map-get($font-weight, l1);
		padding: map-get($space, l2) 0;
		text-align: center;
		color: $color-white;
		text-transform: uppercase;
		border-top: map-get($space, s8) solid map-get($color-grey, d1);
	}
}
