.product-card {
	@extend .u-heading-font;
	border-radius: map-get($space, s7);
	text-align: center;
	padding: map-get($space, l2);
	transition: all map-get($time, main);
	overflow: hidden;

	&:hover {
		box-shadow: 0 1px 2px #707070;
	}

	&__title {
		font-size: map-get($space, l2);
		text-transform: capitalize;
		font-weight: map-get($font-weight, l1);
		color: map-get($color-grey, d2);
	}

	&__image {
		margin: map-get($space, l1) auto;
		height: map-get($space, l3) * 4;
		width: auto;
		transition: all map-get($time, l3);
		@include rwd-df(xs) {
			height: map-get($space, l3) * 2.5;
		}
	}

	&:hover &__image {
		transform: scale(1.2);
	}

	&__link {
		&::before {
			content: '';
			position: absolute;
			top: 0;
			#{$start}: 0;
			width: 22%;
			padding: 22%;
			border-radius: 50%;
			background-color: #fff;
			z-index: -1;
		}

		&:link,
		&:visited {
			font-size: map-get($font-size, s1);
			font-weight: map-get($font-weight, l2);
			color: map-get($color-grey, main);
			transition: all 0.3s;

			position: relative;
			z-index: 2;
			display: inline-block;
			padding: map-get($space, main);
			// background-color: red;
		}
		&:hover,
		&:active {
			color: map-get($color-primary, main);
			text-decoration: underline;
		}
		&__text {
		}
	}
}
