.modal {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba($color-black, 0.75);
	display: flex;
	justify-content: center;
	align-items: center;
	transition: all map-get($time, l3);

	opacity: 0;
	visibility: hidden;
	transform: scaleY(0);
	transform-origin: top center;
	z-index: map-get($z-index, s1);

	&.show {
		opacity: 1;
		visibility: visible;
		z-index: map-get($z-index, xxl3);
		transform: scaleY(1);
	}

	&__content {
		position: relative;
		background-image: url('../../assets/img/newsletter/newsletterbg.jpg');
		box-shadow: 0 0 7px 1px map-get($color-grey, d2);
		background-position: center center;
		background-repeat: no-repeat;
		background-size: cover;
		padding: map-get($space, l3);
		text-align: center;
		width: 35%;

		@include rwd-df(xl) {
			width: 50%;
		}

		@include rwd-df(md) {
			width: 60%;
		}

		@include rwd-df(sm) {
			width: 75%;
		}

		@include rwd-df(xs) {
			width: 90%;
			padding: map-get($space, l2);
		}
	}

	&__title {
		color: map-get($color-grey, d2);
		font-size: map-get($font-size, l4);
		font-weight: map-get($font-weight, l2);
		text-align: center;
		text-transform: uppercase;
		margin-bottom: map-get($space, l2);
	}

	&__desc {
		color: map-get($color-grey, d1);
		font-size: map-get($space, main);
		text-align: center;
		margin-bottom: map-get($space, l1);
	}

	&__btn--close {
		position: absolute;
		top: 0;
		right: 0;
		border: map-get($space, s8) solid $color-white;
		display: block;
		background-color: map-get($color-grey, l1);
		transform: translate(50%, -50%);
		transition: all map-get($time, main);

		.icon {
			color: $color-white;
			padding: map-get($space, s6);
			@include squareSize(map-get($space, l2));
			pointer-events: none;
		}

		&:hover {
			background-color: map-get($color-grey, d1);
		}
	}

	.newsletters-form {
		input[type='email'] {
			display: block;
			width: 80%;
			background: map-get($color-grey, l7);
			border-radius: 0;
			font-size: map-get($font-size, main);
			padding: map-get($space, main);
			margin: 0 auto map-get($space, l1);
			width: 70%;
			@include rwd-df(xs) {
				width: 100%;
			}
		}
		button[type='submit'] {
			background-color: map-get($color-primary, main);
			color: $color-white;
			padding: map-get($space, s2) map-get($space, l2);
			border: map-get($space, s7) solid transparent;
			text-transform: capitalize;
			font-size: map-get($font-size, main);
			font-weight: map-get($font-weight, l2);
			text-transform: uppercase;
			transition: all map-get($time, main);

			&:hover {
				border-color: map-get($color-primary, main);
				background-color: $color-white;
				color: map-get($color-primary, main);
			}
		}
	}
}
