.internet-notify {
	position: fixed;
	top: map-get($space, l7);
	#{$start}: 0;
	font-size: map-get($font-size, s1);
	color: map-get($color-grey, l6);
	background-color: map-get($color-grey, d1);
	border-radius: map-get($space, main);
	padding: map-get($space, s3) map-get($space, s1);

	display: flex;
	align-items: center;
	gap: map-get($space, s1);

	opacity: 1;
	visibility: visible;
	transform: scale(1);
	transform-origin: #{$start} center;
	z-index: map-get($z-index, xxl3);
	transition: all map-get($time, l3);

	&.hidden {
		opacity: 0;
		visibility: hidden;
		transform: scale(0);
		z-index: map-get($z-index, s1);
		pointer-events: none;
	}

	&__icon {
		@include squareSize(map-get($space, l1));

		&--on {
			color: #10b981;
		}
		&--off {
			color: #dc2626;
		}
	}

	&_message {
	}

	&__btn {
		&--close {
		}

		&--reload {
		}

		.icon {
			background-color: map-get($color-grey, main);
			border-radius: 50%;
			padding: map-get($space, s6);
			@include squareSize(map-get($space, l1));
			transition: all map-get($time, main);
			pointer-events: none;
		}

		&:hover .icon {
			background-color: map-get($color-grey, l6);
			color: map-get($color-grey, d1);
			transform: rotate(1turn);
		}
	}
}
