.toggler-btn {
	$bar-height: map-get($space, s5);
	border: map-get($space, s8) solid map-get($color-grey, main);
	padding: map-get($space, s5);
	border-radius: map-get($space, s4);
	z-index: map-get($z-index, xxl2);

	display: none;

	@include rwd-df(md) {
		display: block;
	}

	&__bars-container {
		display: block;
		width: map-get($space, l3);
		height: map-get($space, l2);
		// background-color: lightgrey;
		position: relative;

		&__bar {
			display: block;
			background-color: map-get($color-grey, main);
			height: $bar-height;
			border-radius: map-get($space, main);

			position: absolute;
			#{$start}: 0;
			width: 100%;
			transition: top map-get($time, l3) map-get($time, l3),
				opacity map-get($time, l3) map-get($time, l3),
				transform map-get($time, l3);

			&:nth-child(1) {
				top: 0;
			}
			&:nth-child(2) {
				top: calc(50% - $bar-height / 2);
			}
			&:nth-child(3) {
				top: calc(100% - $bar-height);
			}
		}
	}

	&.open &__bars-container__bar {
		top: calc(50% - $bar-height / 2);
		transition: top map-get($time, l3), opacity map-get($time, l3),
			transform map-get($time, l3) map-get($time, l3);

		&:nth-child(1) {
			transform: rotate(45deg);
		}
		&:nth-child(2) {
			opacity: 0;
		}
		&:nth-child(3) {
			transform: rotate(-45deg);
		}
	}
}
